.neon-text {
  background: #000;
  color: #fff;
  animation: 2s ease infinite reverse glow;
}

.neon-text:before {
  content: attr(data-text);
  position: absolute;
  mix-blend-mode: difference;
  filter: blur(3px);
}

.neon-text:after {
  content: "";
  background: linear-gradient(45deg, #7b4397 30%, #dc2430 70%);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  mix-blend-mode: multiply;
}

@keyframes glow {
  0% {
    text-shadow: 0 0;
  }

  75% {
    text-shadow: 0 0 16px;
    filter: brightness(300%);
  }
}